import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
import * as constants from "../../js/constants";
import * as actions from "../../js/actions";
import * as utils from "../../js/utils";
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
import HeaderComponent from "../../components/HeaderComponent";
import TitleComponent from "../../components/TitleComponent";
import FooterComponent from "../../components/FooterComponent";
import StoreStatusComponent from "../../components/StoreStatusComponent";
import MessageComponent from "../../components/MessageComponent";
import LoaderComponent from "../../components/LoaderComponent";
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

const VacancyCreatePage = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const id = useParams().id;

  const [qualification, qualificationSet] = useState("");
  const [rank, rankSet] = useState("");
  const [image, imageSet] = useState(null);
  const [sphere, sphereSet] = useState("");
  const [education, educationSet] = useState("");
  const [experience, experienceSet] = useState("");
  const [schedule, scheduleSet] = useState("");
  const [description, descriptionSet] = useState("");

  const vacancyCreateStore = useSelector((state) => state.vacancyCreateStore);
  const {
    // load: loadVacancyCreate,
    data: dataVacancyCreate,
    // error: errorVacancyCreate,
    // fail: failVacancyCreate,
  } = vacancyCreateStore;

  useEffect(() => {
    if (dataVacancyCreate) {
      utils.Sleep(2000).then(() => {
        dispatch({ type: constants.VACANCY_CREATE_RESET_CONSTANT });
        dispatch({ type: constants.VACANCY_LIST_RESET_CONSTANT });
        navigate("/vacancy_list");
      });
    }
  }, [dataVacancyCreate]);

  const formHandlerSubmit = (e) => {
    e.preventDefault();
    const form = {
      "Action-type": "VACANCY_CREATE",
      qualification: qualification,
      rank: rank,
      sphere: sphere,
      education: education,
      experience: experience,
      schedule: schedule,
      image: image,
      description: description,
    };
    dispatch(actions.vacancyCreateAuthAction(form));
  };

  return (
    <div>
      <HeaderComponent logic={true} redirect={true} />
      <TitleComponent
        first={" "}
        second={"       ."}
      />
      <main className="container p-0">
        <div className="m-0 p-0">
          {StoreStatusComponent(
            vacancyCreateStore,
            "vacancyCreateStore",
            true,
            "",
            constants.DEBUG_CONSTANT
          )}
        </div>
        <div className="btn-group p-1 m-0 text-start w-100">
          <Link to={"/vacancy_list"} className="btn btn-sm btn-primary">
            {"<="}   
          </Link>
        </div>
        {!dataVacancyCreate && (
          <div className="container-fluid">
            <ul className="row-cols-auto row-cols-md-auto row-cols-lg-auto justify-content-center p-0 m-0">
              <form autoComplete="on" className="" onSubmit={formHandlerSubmit}>
                <div className="p-0 m-0">
                  <h6 className="lead"> </h6>
                </div>
                <div className="p-0 m-0">
                  <label className="w-50 form-control-md m-1">
                    :
                    <input
                      type="text"
                      value={qualification}
                      required
                      minLength="1"
                      maxLength="256"
                      placeholder="  ..."
                      className="form-control form-control-sm"
                      onChange={(e) => qualificationSet(e.target.value)}
                    />
                    <small className="text-danger">* </small>
                    <p className="p-0 m-0">
                      <small className="text-muted">
                        :   256 
                      </small>
                    </p>
                  </label>
                  <label className="form-control-md m-1">
                    :
                    <input
                      type="text"
                      placeholder="  ..."
                      value={rank}
                      minLength="1"
                      maxLength="32"
                      className="form-control form-control-sm"
                      onChange={(e) => rankSet(e.target.value)}
                    />
                    <small className="text-secondary">*  </small>
                    <p className="p-0 m-0">
                      <small className="text-muted">
                        :   32 
                      </small>
                    </p>
                  </label>
                </div>
                <br />
                <div className="p-0 m-0">
                  <label className="form-control-md m-1">
                    :
                    <input
                      type="file"
                      accept=".jpg, .png"
                      className="form-control form-control-sm"
                      onChange={(e) => imageSet(e.target.files[0])}
                    />
                    <small className="text-muted">*  </small>
                  </label>
                  <label className="form-control-md m-1">
                    :
                    <select
                      value={sphere}
                      required
                      className="form-control form-control-sm"
                      onChange={(e) => sphereSet(e.target.value)}
                    >
                      <option value=""> </option>
                      <option value=""></option>
                      <option value=" ">
                         
                      </option>
                    </select>
                    <small className="text-danger">* </small>
                  </label>
                </div>
                <br />
                <div className="p-0 m-0">
                  <label className="form-control-md m-1">
                    :
                    <select
                      value={education}
                      required
                      className="form-control form-control-sm"
                      onChange={(e) => educationSet(e.target.value)}
                    >
                      <option value=""> </option>
                      <option value=", -">
                         / -
                      </option>
                      <option value=""></option>
                      <option value="-">
                        -
                      </option>
                      <option value=""></option>
                    </select>
                    <small className="text-danger">* </small>
                  </label>
                  <label className="form-control-md m-1">
                    :
                    <select
                      value={experience}
                      required
                      className="form-control form-control-sm"
                      onChange={(e) => experienceSet(e.target.value)}
                    >
                      <option value=""> </option>
                      <option value="  ">
                          
                      </option>
                      <option value=" 1   3 ">
                         1   3 
                      </option>
                      <option value=" 3  6 "> 3  6 </option>
                      <option value=" 6 "> 6 </option>
                    </select>
                    <small className="text-danger">* </small>
                  </label>
                  <label className="form-control-md m-1">
                    :
                    <select
                      value={schedule}
                      required
                      className="form-control form-control-sm"
                      onChange={(e) => scheduleSet(e.target.value)}
                    >
                      <option value=""> </option>
                      <option value=" (5/2)"> </option>
                      <option value=""></option>
                      <option value=" "> </option>
                      <option value=""></option>
                    </select>
                    <small className="text-danger">* </small>
                  </label>
                </div>
                <br />
                <div className="p-0 m-0">
                  <label className="w-75 form-control-md m-1">
                    :
                    <textarea
                      id="short_description_char_field"
                      name="short_description_char_field"
                      placeholder="  ..."
                      value={description}
                      minLength="1"
                      maxLength="512"
                      rows="2"
                      className="form-control form-control-sm"
                      onChange={(e) => descriptionSet(e.target.value)}
                    />
                    <small className="text-secondary">*  </small>
                    <p className="p-0 m-0">
                      <small className="text-muted">
                        :   512 
                      </small>
                    </p>
                  </label>
                </div>
                <br />
                <div className="container-fluid text-center">
                  <ul className="row row-cols-auto row-cols-md-auto row-cols-lg-auto nav justify-content-center">
                    <li className="m-1">
                      <button
                        className="btn btn-sm btn-outline-primary"
                        type="submit"
                      >
                        
                      </button>
                    </li>
                    <li className="m-1">
                      <button
                        className="btn btn-sm btn-outline-warning"
                        type="reset"
                      >
                          
                      </button>
                    </li>
                  </ul>
                </div>
              </form>
            </ul>
          </div>
        )}
      </main>
      <FooterComponent />
    </div>
  );
};

export default VacancyCreatePage;
