import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
import { adminCreateOrChangeUsersAuthAction } from "../../js/actions";
import HeaderComponent from "../../components/HeaderComponent";
import TitleComponent from "../../components/TitleComponent";
import MessageComponent from "../../components/MessageComponent";
import LoaderComponent from "../../components/LoaderComponent";
import FooterComponent from "../../components/FooterComponent";
import { ADMIN_CREATE_OR_CHANGE_USERS_RESET_CONSTANT } from "../../js/constants";
////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

const AdminCreateOrChangeUsersPage = () => {
  const dispatch = useDispatch();

  const [changeUser, setChangeUser] = useState(
    "   "
  );
  const [changeUserPassword, setChangeUserPassword] = useState(
    "   "
  );
  const [clearUserGroups, setClearUserGroups] = useState(
    "     "
  );
  const [additionalExcel, setAdditionalExcel] = useState(null);

  const adminCreateOrChangeUsersStore = useSelector(
    (state) => state.adminCreateOrChangeUsersStore
  );
  const {
    load: loadRationalCreate,
    data: dataRationalCreate,
    error: errorRationalCreate,
    fail: failRationalCreate,
  } = adminCreateOrChangeUsersStore;

  useEffect(() => {
    if (dataRationalCreate) {
      sleep(5000).then(() => {
        dispatch({
          type: ADMIN_CREATE_OR_CHANGE_USERS_RESET_CONSTANT,
        });
        setChangeUser("   ");
        setChangeUserPassword("   ");
        setClearUserGroups("     ");
        setAdditionalExcel(null);
      });
    }
  }, [dataRationalCreate]);

  const submitHandler = (e) => {
    e.preventDefault();
    const form = {
      "Action-type": "CREATE_OR_CHANGE_USERS",
      changeUser: changeUser,
      changeUserPassword: changeUserPassword,
      clearUserGroups: clearUserGroups,
      additionalExcel: additionalExcel,
    };
    dispatch(adminCreateOrChangeUsersAuthAction(form));
  };

  function sleep(time) {
    return new Promise((resolve) => setTimeout(resolve, time));
  }

  return (
    <div>
      <HeaderComponent logic={true} redirect={true} />
      <TitleComponent
        first={"   "}
        second={
          "           ."
        }
      />
      <main className="container text-center">
        <div className="text-center">
          {loadRationalCreate && <LoaderComponent />}
          {dataRationalCreate && (
            <div className="m-1">
              <MessageComponent variant="success">
                  !
              </MessageComponent>
            </div>
          )}
          {errorRationalCreate && (
            <div className="m-1">
              <MessageComponent variant="danger">
                {errorRationalCreate}
              </MessageComponent>
            </div>
          )}
          {failRationalCreate && (
            <div className="m-1">
              <MessageComponent variant="warning">
                {failRationalCreate}
              </MessageComponent>
            </div>
          )}
        </div>
        {!dataRationalCreate && (
          <div className="container-fluid text-center">
            <ul className="row row-cols-auto row-cols-md-auto row-cols-lg-auto nav justify-content-center">
              <div className="container">
                <br />
                <form
                  method="POST"
                  target="_self"
                  encType="multipart/form-data"
                  name="RATIONAL_CREATE"
                  autoComplete="on"
                  className="text-center"
                  onSubmit={submitHandler}
                >
                  <div>
                    <div className="">
                      <div>
                        <h6 className="lead">
                                 
                        </h6>
                        <a
                          className="btn btn-md btn-success m-1"
                          href="/static/media/admin/account/create_users.xlsx"
                        >
                           excel-
                        </a>
                      </div>
                      <div>
                        <label className="form-control-sm m-1">
                               :
                          <select
                            id="changeUser"
                            name="changeUser"
                            required
                            className="form-control form-control-sm"
                            value={changeUser}
                            onChange={(e) => setChangeUser(e.target.value)}
                          >
                            <option value="   ">
                                 
                            </option>
                            <option value="    ">
                                  
                            </option>
                          </select>
                          <small className="text-danger">* </small>
                        </label>
                        <label className="form-control-sm m-1">
                                :
                          <select
                            id="changeUserPassword"
                            name="changeUserPassword"
                            required
                            className="form-control form-control-sm"
                            value={changeUserPassword}
                            onChange={(e) =>
                              setChangeUserPassword(e.target.value)
                            }
                          >
                            <option value="   ">
                                 
                            </option>
                            <option value="    ">
                                  
                            </option>
                          </select>
                          <small className="text-danger">* </small>
                        </label>
                        <label className="form-control-sm m-1">
                                :
                          <select
                            id="clearUserGroups"
                            name="clearUserGroups"
                            required
                            className="form-control form-control-sm"
                            value={clearUserGroups}
                            onChange={(e) => setClearUserGroups(e.target.value)}
                          >
                            <option value="     ">
                                   
                            </option>
                            <option value="    ">
                                  
                            </option>
                          </select>
                          <small className="text-danger">* </small>
                        </label>
                      </div>
                      <br />
                      <div>
                        <label className="form-control-sm m-1">
                          Excel   :
                          <input
                            type="file"
                            id="AdditionalExcel"
                            name="AdditionalExcel"
                            required
                            accept=".xlsx, .xls"
                            className="form-control form-control-sm"
                            onChange={(e) =>
                              setAdditionalExcel(e.target.files[0])
                            }
                          />
                          <small className="text-danger">* </small>
                        </label>
                      </div>
                      <br />
                      <div className="container-fluid text-center">
                        <ul className="row row-cols-auto row-cols-md-auto row-cols-lg-auto nav justify-content-center">
                          <li className="m-1">
                            <button
                              className="btn btn-sm btn-outline-primary"
                              type="submit"
                            >
                              
                            </button>
                          </li>
                          <li className="m-1">
                            <button
                              className="btn btn-sm btn-outline-warning"
                              type="reset"
                            >
                                
                            </button>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </form>
                <br />
              </div>
            </ul>
          </div>
        )}
      </main>
      <FooterComponent />
    </div>
  );
};

export default AdminCreateOrChangeUsersPage;
